<script>
import Container from './components/Container.vue';
import postdata from './assets/postdata.js';
import axios from 'axios'

export default {
  name: 'App',
  data() {
      return {
          postdatas : postdata,
          morePost : 0,
      }
  },
  components: {
    Container,
  },
  methods : {
    more() {
      // post ÏöîÏ≤≠
      // axios.post('url', {name : 'kim'}.then().catch(err)=>{
      //   err
      // })
      // get ÏöîÏ≤≠
      axios.get(`https://codingapple1.github.io/vue/more${this.morePost}.json`).then( e=> {
        this.postdatas.push(e.data);
        this.morePost++;
      })
    }
  },
}
</script>

